import { Button } from '@mui/material';
import React from 'react';
import { useForm } from "react-hook-form";
import VpnKeyIcon from '@mui/icons-material/VpnKey';
import { useContext } from 'react';
import { allUserData } from '../context/context';
import { CHECK_EMAIL } from '../../utils/constants';
import '../../formAuthReg.css'


export const FormReg = ({ authReg, handleClose, setAuthReg }) => {

    const { singUp } = useContext({ ...allUserData })



    const { register, handleSubmit, formState: { errors, isValid } } = useForm({
        mode: 'all',
        defaultValues: {
            group: 'group-7',
        }

    });


    const cbSubmit = (data) => {
        singUp(data)
        handleClose()

    }



    return (


        <form className='authRegForm' onSubmit={handleSubmit(cbSubmit)}>


            <h5 className='authRegForm__header'>
                 
                <br />
                <p className='authRegForm__header_text'>(  ,    ,   )</p>
            </h5>

            <label className='authRegForm__leble'> {errors?.email?.message ? <p className='authRegForm__leble'>{errors?.email?.message}</p> : ' Email'}
                <input className='authRegForm__input'
                    {...register('email', {
                        required: {
                            value: true,
                            message: ' '
                        },
                        minLength: {
                            value: 7,
                            message: ' '
                        },
                        pattern: {
                            value: CHECK_EMAIL,
                            message: ' '
                        }

                    })}
                    type='email'
                    placeholder="shla@Sobaka.poShosse"

                ></input>
            </label>

            <label className='authRegForm__leble'> {errors?.password?.message ? <p className='authRegForm__leble'>{errors?.password?.message}</p> : ' '}
                <input className='authRegForm__input'
                    {...register('password', {
                        required: {
                            value: true,
                            message: ' '
                        },
                        minLength: {
                            value: 8,
                            message: ' '
                        },
                    })}
                    type='password'
                    autoComplete="on"
                    placeholder="   8 "

                ></input>
            </label>

            <label className='authRegForm__leble'> {errors?.password?.message ? <p className='authRegForm__leble'>{errors?.password?.message}</p> : ' '}
                <input className='authRegForm__input'
                    {...register('group', {
                        required: {
                            value: true,
                            message: ' '
                        },
                        minLength: {
                            value: 2,
                            message: ' '
                        },

                    })}
                    type='text'
                    placeholder="  2 "

                ></input>
            </label>

            <div className='authRegForm__BtnBlock'>

                <Button
                    variant='outlined'
                    size='small'
                    onClick={() => { setAuthReg(!authReg) }}>
                    
                </Button>

                <Button type='submit'
                    variant='contained'
                    color='success'
                    size='small'
                    disabled={!isValid}>
                    
                </Button>

            </div>

        </form>

    )
}
