import { Button } from "@mui/material";
import { useContext } from "react";
import { useForm } from "react-hook-form";
import api from "../../utils/api";
import { AllContextData } from "../context/context";
import './formComment.css'


export const FormComment = ({ handleClose2, comments, _id, ...rest }) => {



    const data = useContext(AllContextData)

    const updatePostState = data[4]


      
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: 'onBlur',
            defaultValues: {
            text: '',
        }
    });

    const cbSubmit = ( data ) => {

        api.addNewComments(data, _id).then((newPost) => {updatePostState(newPost)})

        handleClose2()
    }

    return (
        <>
            <form onSubmit={handleSubmit(cbSubmit)} className='form'>

                <label className="labelfor"> {errors?.url?.message ? <p className="paragrafor">{errors?.url?.message}</p> : ' '}
                    <input className="inputfor"
                        {...register('text', {
                            required: {
                                value: true,
                                message: '   '
                            },
                            minLength: {
                                value: 12,
                                message: '     2 '
                            },
   
                        })}
                        type='text'
                        placeholder="    ..." 
      
                    ></input>
                </label>


                <Button type="submit" variant="contained" >{Object.entries(rest).length ? ' ' : ' '}</Button>

            </form>

        </>
    )


}
